//base
@mixin f-base($weight: 400, $lineHight: 1.8) {
	font-style: normal;
	font-weight: $weight;
	line-height: $lineHight;
	color: $black;
	text-align: justify;
	letter-spacing: 0.06em;
	font-feature-settings: 'palt' 1;
}

//
@mixin f-btn($weight: 700, $lineHight: 1.8) {
	font-style: normal;
	font-weight: $weight;
	line-height: $lineHight;
	text-align: center;
	text-indent: 0.1em; //letter-spacing
	letter-spacing: 0.1em;
	font-feature-settings: 'palt' 1;
}

//  --------------------------------------------------

//
@mixin ff-base_yugo() {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
		'Original Yu Gothic', 'Yu Gothic', 'Hiragino Sans', 'Noto Sans CJK JP',
		sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol',
		'Noto Sans Emoji';
}

//
@mixin ff-base_hira() {
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
		'Hiragino Sans', 'Noto Sans CJK JP', 'Original Yu Gothic', 'Yu Gothic',
		sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol',
		'Noto Sans Emoji';
}

//

//
@mixin ff-noto() {
	font-family: 'Noto Sans JP', sans-serif;
}

