// line-height --------------------------------------------------

// lineHeightCropline-heightmixin
// f-variables.scssscss
@mixin lineHeightCrop($line-height) {
	&::before {
		display: block;
		width: 0;
		height: 0;
		margin-top: calc((1 - #{$line-height}) * 0.5em);
		content: '';
	}
	&::after {
		display: block;
		width: 0;
		height: 0;
		margin-bottom: calc((1 - #{$line-height}) * 0.5em);
		content: '';
	}
}
