// flexbox  ________________________________

//align-items
@mixin align-items($justify) {
	&Center {
		align-items: center;
		justify-content: $justify;
	}
	&Stretch {
		align-items: stretch;
		justify-content: $justify;
	}
	&Start {
		align-items: flex-start;
		justify-content: $justify;
	}
	&End {
		align-items: flex-end;
		justify-content: $justify;
	}
	&Base {
		align-items: baseline;
		justify-content: $justify;
	}
}

//justifyalign
@mixin justify-align {
	&.-start {
		$justify: start;
		@include align-items(start);
	}

	&.-end {
		@include align-items(end);
	}

	&.-center {
		@include align-items(center);
	}

	&.-between {
		@include align-items(space-between);
	}

	&.-around {
		@include align-items(space-around);
	}

	&.-evenly {
		@include align-items(space-evenly);
	}

	&.-stretch {
		@include align-items(stretch);
	}
}

// flexbox  ___________________________________

.flex {
	display: flex;
	flex-flow: row nowrap;

	&.-reverse {
		flex-flow: row-reverse nowrap;
	}

	@include justify-align();
}

.inlineFlex {
	display: inline-flex;
	flex-flow: row nowrap;

	&.-reverse {
		flex-flow: row-reverse nowrap;
	}

	@include justify-align();
}

.flexCol {
	display: flex;
	flex-flow: column nowrap;

	&.-reverse {
		flex-flow: column-reverse nowrap;
	}

	@include justify-align();
}

.inlineFlexCol {
	display: inline-flex;
	flex-flow: column nowrap;

	&.-reverse {
		flex-flow: column-reverse nowrap;
	}

	@include justify-align();
}

.flexWrap {
	display: flex;
	flex-flow: row wrap;

	&.-reverse {
		flex-flow: row-reverse wrap;
	}

	&.-wrapReverse {
		flex-flow: row wrap-reverse;
	}

	&.-reverseReverse {
		flex-flow: row-reverse wrap-reverse;
	}

	@include justify-align();
}

.inlineFlexWrap {
	display: inline-flex;
	flex-flow: row wrap;

	&.-reverse {
		flex-flow: row-reverse wrap;
	}

	&.-wrapReverse {
		flex-flow: row wrap-reverse;
	}

	&.-reverseReverse {
		flex-flow: row-reverse wrap-reverse;
	}

	@include justify-align();
}

.flexColWrap {
	display: flex;
	flex-flow: column wrap;

	&.-reverse {
		flex-flow: column-reverse wrap;
	}

	&.-wrapReverse {
		flex-flow: column wrap-reverse;
	}

	@include justify-align();
}

.inlineFlexColWrap {
	display: inline-flex;
	flex-flow: column wrap;

	&.-reverse {
		flex-flow: column-reverse wrap;
	}

	&.-wrapReverse {
		flex-flow: column wrap-reverse;
	}

	@include justify-align();
}

// flexbox  __________________________________________

.flexAuto {
	flex: auto;
}

.flexNone {
	flex: none;
}

.order {
	&M1 {
		order: -1;
	}
}

